@charset "utf-8";

.MovieList{
  width: 1200px;
  margin: 0 auto;
  margin-top: 1rem;
  display: flex; box-sizing: border-box;
  flex-wrap: wrap;
  justify-content: space-between;
}
.MovieList li{
  width: 300px;
  padding: 0.5rem; box-sizing: border-box;
}
.MovieList img{width: 100%;}
.MovieList p:nth-of-type(1){
  display: inline-block;
  float: right;
  border: 1px solid #333;
  padding-inline: 0.5rem;
  margin-left: 0.5rem;
}
.MovieList p:nth-of-type(2){
  font-size: 18px;
  font-weight: bold;
  -webkit-line-clamp: 1;
}
.MovieList p:nth-of-type(3){
  -webkit-line-clamp: 2;
  margin-bottom: 3rem;
  clear: both;
}
.MovieList p:nth-of-type(2), .MovieList p:nth-of-type(3){
  box-sizing: border-box;
  text-overflow: ellipsis;
  overflow: hidden;
  word-break: break-all;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}
.load_img{
  display: block;
  margin: 15rem auto;
}